import React from "react";
import { Box, Grid, Typography, TextField, Button } from "@mui/material";
import PinDropIcon from "@mui/icons-material/PinDrop";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import MailOutlineIcon from "@mui/icons-material/MailOutline";
import Images from "src/assets";
import ReactPhoneInput from "react-phone-input-2";
import { useFormik } from "formik";
import * as Yup from "yup";
import { callbackRSA } from "src/services";

interface FormData {
  name: string;
  phone: {
    countryCode: string;
    number: string;
  };
  email: string;
  queryDescription: string;
}

const GetInTouch: React.FC = () => {
  const formik = useFormik<FormData>({
    initialValues: {
      name: "",
      phone: { countryCode: "", number: "" },
      email: "",
      queryDescription: "",
    },
    validationSchema: Yup.object({
      name: Yup.string().trim().required("Name is required"),
      phone: Yup.object().shape({
        countryCode: Yup.string().required("Country code is required"),
        number: Yup.string().required("Phone number is required"),
      }),
      email: Yup.string()
        .email("Invalid email address")
        .required("Email is required"),
      queryDescription: Yup.string()
        .trim()
        .required("Please provide a reason"),
    }),
    onSubmit: async (values) => {
      try {
        // eslint-disable-next-line
        const response = await callbackRSA(values);
      } catch (error) {
        console.error("Error submitting form:", error);
      }
    },
  });

  const handlePhoneChange = (value: string, data: any) => {
    const countryCode = `+${data?.dialCode}`;
    const number = value.replace(data?.dialCode, "").trim();
    formik.setFieldValue("phone", { countryCode, number });
  };

  return (
    <Box>
      {/* Section with limited background height */}
      <div className="touch-1">
        <h2>Get in Touch</h2>
        <h6>Reach out to us for more information and assistance</h6>
      </div>

      {/* Main content section */}
      <Box
        sx={{
          backgroundColor: "#FFF",
          padding: "50px 0",
        }}
      >
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
          }}
        >
          <Box
            sx={{
              width: "80%",
              maxWidth: "1200px",
              backgroundColor: "#FFF",
              borderRadius: "16px",
              boxShadow: "0px 4px 16px rgba(0, 0, 0, 0.1)",
              overflow: "hidden",
            }}
          >
            <Grid container>
              {/* Left Section */}
              <Grid
                item
                xs={12}
                md={6}
                sx={{
                  backgroundColor: "rgba(209, 211, 249, 1)",
                  padding: "30px",
                }}
              >
                <img
                  src={Images.office}
                  alt="One Window"
                  style={{
                    width: "100%",
                    height: "auto",
                    borderRadius: "12px",
                    marginBottom: "20px",
                  }}
                />
                <Typography variant="h6" sx={{ fontWeight: "600", marginBottom: "10px" }}>
                  Contact Information
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    color: "#3B3F76",
                    marginBottom: "16px",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                  }}
                >
                  <PinDropIcon
                    sx={{
                      background: "white",
                      padding: "5px",
                      borderRadius: "50%",
                      fontSize: "32px",
                      color: "#3B3F76",
                    }}
                  />
                  915, 8th Floor, Vasavi's MPM Grand, Near Metro Station
                  Ameerpet, Hyderabad, Telangana 500073
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    color: "#3B3F76",
                    marginBottom: "16px",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                  }}
                >
                  <LocalPhoneIcon
                    sx={{
                      background: "white",
                      padding: "5px",
                      borderRadius: "50%",
                      fontSize: "32px",
                      color: "#3B3F76",
                    }}
                  />
                  9154898944
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    color: "#3B3F76",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                  }}
                >
                  <MailOutlineIcon
                    sx={{
                      background: "white",
                      padding: "5px",
                      borderRadius: "50%",
                      fontSize: "32px",
                      color: "#3B3F76",
                    }}
                  />
                  info@onewindow.co
                </Typography>
              </Grid>

              {/* Right Section */}
              <Grid item xs={12} md={6} sx={{ padding: "30px" }}>
                <Typography variant="h6" sx={{ fontWeight: "600", marginBottom: "10px" }}>
                  Letâ€™s Talk
                </Typography>
                <Typography
                  variant="body1"
                  sx={{ color: "#3B3F76", marginBottom: "20px" }}
                >
                  Contact us directly or fill out the form, and we will get back to you promptly.
                </Typography>
                <form onSubmit={formik.handleSubmit}>
                  <TextField
                    label="Name"
                    name="name"
                    variant="outlined"
                    fullWidth
                    value={formik.values.name}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    error={formik.touched.name && Boolean(formik.errors.name)}
                    helperText={formik.touched.name && formik.errors.name}
                    sx={{ marginBottom: "16px" }}
                    size="small"
                  />
                  <ReactPhoneInput
                    value={`${formik.values.phone.countryCode}${formik.values.phone.number}`}
                    onChange={handlePhoneChange}
                    country="in"
                    inputProps={{
                      name: "phone",
                      required: true,
                    }}
                    inputStyle={{
                      width: "100%",
                      height: "40px",
                    }}
                  />
                  {formik.touched.phone?.number && formik.errors.phone?.number && (
                    <Typography color="error" variant="body2" sx={{ marginTop: "8px" }}>
                      {formik.errors.phone.number}
                    </Typography>
                  )}
                  <TextField
                    label="Email"
                    name="email"
                    type="email"
                    variant="outlined"
                    fullWidth
                    value={formik.values.email}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    error={formik.touched.email && Boolean(formik.errors.email)}
                    helperText={formik.touched.email && formik.errors.email}
                    sx={{ marginBottom: "16px", marginTop: "16px" }}
                    size="small"
                  />
                  <TextField
                    label="Reason"
                    name="queryDescription"
                    variant="outlined"
                    fullWidth
                    multiline
                    rows={4}
                    value={formik.values.queryDescription}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    error={
                      formik.touched.queryDescription &&
                      Boolean(formik.errors.queryDescription)
                    }
                    helperText={
                      formik.touched.queryDescription &&
                      formik.errors.queryDescription
                    }
                    sx={{ marginBottom: "16px" }}
                    size="small"
                  />
                  <Button
                    type="submit"
                    variant="contained"
                    sx={{
                      backgroundColor: "rgba(59, 63, 118, 1)",
                      color: "#FFF",
                      padding: "10px 30px",
                      borderRadius: "50px",
                      justifyContent: "center",
                      "&:hover": {
                        backgroundColor: "rgba(59, 63, 118, 1)",
                      },
                    }}
                  >
                    Submit
                  </Button>
                </form>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default GetInTouch;
